.dark-mode{
    background-color: darken($grisOscuro,10);
    p{
        color: $blanco;
    }

    a,h1,h2,h3,h4,blockquote{
        color: $gris;
    }

    .anuncio{
        background-color: $grisOscuro;
        border: 1px solid $grisOscuro;

        .precio{
            color: $verde;
        }

    }

    .darkico{
        filter: invert(100%);
    }

    .formulario{
        label,
        legend,
        p{
            color: $gris;
        }

        input:not([type = "submit"]),
        textarea,
        select{
            background-color: $grisOscuro;
            border: 1px solid $grisOscuro;
            color: $blanco;
        }
    }

    table{
        color: $blanco;
    }
}