.testimonial{
    background-color: $verde;
    font-size: 2rem;
    padding: 2rem;
    color: $blanco;
    border-radius: 2rem;


    blockquote{
        padding-left: 5rem;
        position: relative;
        &::before{
            content: "";
            background-image: url(../img/comilla.svg);
            background-repeat: no-repeat;
            width: 4rem;
            height: 5rem;
            display: block;
            position: absolute;
            left: 0rem;

        }
    }

    p{
        text-align: right;
        color: $blanco;
    }
}

